	module.exports = {"data":{"markdownRemark":{"html":"<!---\nThis is an autogenerated file!\n\nPlease do not edit this file directly, but instead make changes to the\nsigs.yaml file in the project root.\n\nTo understand how this file is generated, see https://git.k8s.io/community/generator/README.md\n-->\n<h1>Cluster Lifecycle Special Interest Group</h1>\n<p>The Cluster Lifecycle SIG examines how we should change Kubernetes to make it easier to manage and operate with a focus on cluster deployment and upgrades.</p>\n<h2>Meetings</h2>\n<ul>\n<li>\n<p>Regular SIG Meeting: <a href=\"https://zoom.us/j/166836%E2%80%8B624\">Tuesdays at 09:00 PT (Pacific Time)</a> (weekly). <a href=\"http://www.thetimezoneconverter.com/?t=09:00&#x26;tz=PT%20%28Pacific%20Time%29\">Convert to your timezone</a>.</p>\n<ul>\n<li><a href=\"https://docs.google.com/a/weave.works/document/d/1deJYPIF4LmhGjDVaqrswErIrV7mtwJgovtLnPCDxP7U/edit\">Meeting notes and Agenda</a>.</li>\n<li><a href=\"https://www.youtube.com/playlist?list=PL69nYSiGNLP29D0nYgAGWt1ZFqS9Z7lw4\">Meeting recordings</a>.</li>\n</ul>\n</li>\n<li>\n<p>kubeadm Office Hours: <a href=\"https://zoom.us/j/166836%E2%80%8B624\">Wednesdays at 09:00 PT (Pacific Time)</a> (weekly). <a href=\"http://www.thetimezoneconverter.com/?t=09:00&#x26;tz=PT%20%28Pacific%20Time%29\">Convert to your timezone</a>.</p>\n<ul>\n<li><a href=\"https://docs.google.com/document/d/130_kiXjG7graFNSnIAgtMS1G8zPDwpkshgfRYS0nggo/edit\">Meeting notes and Agenda</a>.</li>\n<li><a href=\"https://www.youtube.com/playlist?list=PL69nYSiGNLP29D0nYgAGWt1ZFqS9Z7lw4\">Meeting recordings</a>.</li>\n</ul>\n</li>\n<li>\n<p>Cluster API working group: <a href=\"https://zoom.us/j/166836%E2%80%8B624\">Wednesdays at 10:00 PT (Pacific Time)</a> (weekly). <a href=\"http://www.thetimezoneconverter.com/?t=10:00&#x26;tz=PT%20%28Pacific%20Time%29\">Convert to your timezone</a>.</p>\n<ul>\n<li><a href=\"https://docs.google.com/document/d/16ils69KImmE94RlmzjWDrkmFZysgB2J4lGnYMRN89WM/edit#\">Meeting notes and Agenda</a>.</li>\n<li><a href=\"https://www.youtube.com/playlist?list=PL69nYSiGNLP29D0nYgAGWt1ZFqS9Z7lw4\">Meeting recordings</a>.</li>\n</ul>\n</li>\n<li>\n<p>kops Office Hours: <a href=\"https://zoom.us/my/k8ssigaws\">Fridays at 09:00 PT (Pacific Time)</a> (biweekly). <a href=\"http://www.thetimezoneconverter.com/?t=09:00&#x26;tz=PT%20%28Pacific%20Time%29\">Convert to your timezone</a>.</p>\n<ul>\n<li><a href=\"https://docs.google.com/document/d/12QkyL0FkNbWPcLFxxRGSPt_tNPBHbmni3YLY-lHny7E/edit\">Meeting notes and Agenda</a>.</li>\n</ul>\n</li>\n</ul>\n<h2>Leadership</h2>\n<h3>Chairs</h3>\n<p>The Chairs of the SIG run operations and processes governing the SIG.</p>\n<ul>\n<li>Luke Marsden (<strong><a href=\"https://github.com/lukemarsden\">@lukemarsden</a></strong>), Weave</li>\n<li>Robert Bailey (<strong><a href=\"https://github.com/roberthbailey\">@roberthbailey</a></strong>), Google</li>\n<li>Lucas Käldström (<strong><a href=\"https://github.com/luxas\">@luxas</a></strong>), Luxas Labs (occasionally contracting for Weaveworks)</li>\n<li>Timothy St. Clair (<strong><a href=\"https://github.com/timothysc\">@timothysc</a></strong>), Heptio</li>\n</ul>\n<h2>Contact</h2>\n<ul>\n<li><a href=\"https://kubernetes.slack.com/messages/sig-cluster-lifecycle\">Slack</a></li>\n<li><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-cluster-lifecycle\">Mailing list</a></li>\n<li><a href=\"https://github.com/kubernetes/community/labels/sig%2Fcluster-lifecycle\">Open Community Issues/PRs</a></li>\n</ul>\n<h2>Subprojects</h2>\n<p>The following subprojects are owned by sig-cluster-lifecycle:</p>\n<ul>\n<li>\n<p><strong>bootkube</strong></p>\n<ul>\n<li>Owners:</li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes-incubator/bootkube/master/OWNERS\">https://raw.githubusercontent.com/kubernetes-incubator/bootkube/master/OWNERS</a></li>\n</ul>\n</li>\n<li>\n<p><strong>kops</strong></p>\n<ul>\n<li>Owners:</li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes/kops/master/OWNERS\">https://raw.githubusercontent.com/kubernetes/kops/master/OWNERS</a></li>\n</ul>\n</li>\n<li>\n<p><strong>kube-aws</strong></p>\n<ul>\n<li>Owners:</li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes-incubator/kube-aws/master/OWNERS\">https://raw.githubusercontent.com/kubernetes-incubator/kube-aws/master/OWNERS</a></li>\n</ul>\n</li>\n<li>\n<p><strong>kube-deploy</strong></p>\n<ul>\n<li>Owners:</li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes/kube-deploy/master/OWNERS\">https://raw.githubusercontent.com/kubernetes/kube-deploy/master/OWNERS</a></li>\n</ul>\n</li>\n<li>\n<p><strong>kube-up</strong></p>\n<ul>\n<li>Owners:</li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes/kubernetes/master/cluster/OWNERS\">https://raw.githubusercontent.com/kubernetes/kubernetes/master/cluster/OWNERS</a></li>\n</ul>\n</li>\n<li>\n<p><strong>kubeadm</strong></p>\n<ul>\n<li>Owners:</li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes/kubeadm/master/OWNERS\">https://raw.githubusercontent.com/kubernetes/kubeadm/master/OWNERS</a></li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes/kubernetes/master/cmd/kubeadm/OWNERS\">https://raw.githubusercontent.com/kubernetes/kubernetes/master/cmd/kubeadm/OWNERS</a></li>\n</ul>\n</li>\n<li>\n<p><strong>kubernetes-anywhere</strong></p>\n<ul>\n<li>Owners:</li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes/kubernetes-anywhere/master/OWNERS\">https://raw.githubusercontent.com/kubernetes/kubernetes-anywhere/master/OWNERS</a></li>\n</ul>\n</li>\n<li>\n<p><strong>kubespray</strong></p>\n<ul>\n<li>Owners:</li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes-incubator/kubespray/master/OWNERS\">https://raw.githubusercontent.com/kubernetes-incubator/kubespray/master/OWNERS</a></li>\n</ul>\n</li>\n<li>\n<p><strong>minikube</strong></p>\n<ul>\n<li>Owners:</li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes/minikube/master/OWNERS\">https://raw.githubusercontent.com/kubernetes/minikube/master/OWNERS</a></li>\n</ul>\n</li>\n</ul>\n<h2>GitHub Teams</h2>\n<p>The below teams can be mentioned on issues and PRs in order to get attention from the right people.\nNote that the links to display team membership will only work if you are a member of the org.</p>\n<p>The google groups contain the archive of Github team notifications.\nMentioning a team on Github will CC its group.\nMonitor these for Github activity if you are not a member of the team.</p>\n<table>\n<thead>\n<tr>\n<th>Team Name</th>\n<th align=\"center\">Details</th>\n<th align=\"center\">Google Groups</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>@kubernetes/sig-cluster-lifecycle-api-reviews</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-cluster-lifecycle-api-reviews\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-cluster-lifecycle-api-reviews\">link</a></td>\n<td>API Changes and Reviews</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-cluster-lifecycle-bugs</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-cluster-lifecycle-bugs\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-cluster-lifecycle-bugs\">link</a></td>\n<td>Bug Triage and Troubleshooting</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-cluster-lifecycle-feature-requests</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-cluster-lifecycle-feature-requests\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-cluster-lifecycle-feature-requests\">link</a></td>\n<td>Feature Requests</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-cluster-lifecycle-misc</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-cluster-lifecycle-misc\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-cluster-lifecycle-misc\">link</a></td>\n<td>General Discussion</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-cluster-lifecycle-pr-reviews</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-cluster-lifecycle-pr-reviews\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-cluster-lifecycle-pr-reviews\">link</a></td>\n<td>PR Reviews</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-cluster-lifecycle-proposals</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-cluster-lifecycle-proposals\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-cluster-lifecycle-proposals\">link</a></td>\n<td>Design Proposals</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-cluster-lifecycle-test-failures</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-cluster-lifecycle-test-failures\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-cluster-lifecycle-test-failures\">link</a></td>\n<td>Test Failures and Triage</td>\n</tr>\n</tbody>\n</table>\n<!-- BEGIN CUSTOM CONTENT -->\n<!-- END CUSTOM CONTENT -->"},"site":{"siteMetadata":{"sigs":["sig-api-machinery","sig-apps","sig-architecture","sig-auth","sig-autoscaling","sig-aws","sig-azure","sig-big-data","sig-cli","sig-cloud-provider","sig-cluster-lifecycle","sig-cluster-ops","sig-contributor-experience","sig-docs","sig-gcp","sig-ibmcloud","sig-instrumentation","sig-multicluster","sig-network","sig-node","sig-openstack","sig-product-management","sig-release","sig-scalability","sig-scheduling","sig-service-catalog","sig-storage","sig-testing","sig-ui","sig-vmware","sig-windows"]}}},"pathContext":{"slug":"/sig-cluster-lifecycle/"}}

